"use client"

import { Button } from "@/components/ui/button"
import Link from "next/link"
import Image from "next/image"
import { useEffect, useState } from "react"
import { createClient } from "@/lib/supabase/client"
import type { User } from "@supabase/supabase-js"
import { LogoutButton } from "./logout-button"
import { ArrowLeft } from "lucide-react"

interface NavbarClientProps {
  variant?: "default" | "minimal"
  showBackButton?: boolean
  onBack?: () => void
}

export function NavbarClient({ variant = "default", showBackButton = false, onBack }: NavbarClientProps) {
  const [user, setUser] = useState<User | null>(null)
  const [loading, setLoading] = useState(true)
  const [isShopOwner, setIsShopOwner] = useState(false)

  useEffect(() => {
    const checkUser = async () => {
      const supabase = createClient()
      const { data: { user } } = await supabase.auth.getUser()
      setUser(user)
      
      // Check if user is a shop owner
      if (user) {
        const { data: shop } = await supabase
          .from("repair_shops")
          .select("id")
          .eq("owner_id", user.id)
          .maybeSingle()
        setIsShopOwner(!!shop)
      }
      
      setLoading(false)
    }

    checkUser()

    // Listen for auth changes
    const supabase = createClient()
    const { data: { subscription } } = supabase.auth.onAuthStateChange(async (_event, session) => {
      setUser(session?.user ?? null)
      
      // Check shop ownership when auth state changes
      if (session?.user) {
        const { data: shop } = await supabase
          .from("repair_shops")
          .select("id")
          .eq("owner_id", session.user.id)
          .maybeSingle()
        setIsShopOwner(!!shop)
      } else {
        setIsShopOwner(false)
      }
    })

    return () => {
      subscription.unsubscribe()
    }
  }, [])

  const isLoggedIn = !!user

  if (variant === "minimal") {
    return (
      <header className="absolute top-0 w-full">
        <div className="container mx-auto flex h-16 items-center justify-between px-4">
          <Link href="/" className="flex items-center gap-2">
            <Image 
              src="/logo.png" 
              alt="FixWise Logo" 
              width={180} 
              height={60}
              className="h-12 w-auto"
            />
          </Link>
          <nav className="flex items-center gap-2">
            <Link href="/shops">
              <Button variant="ghost" size="sm">Find Shops</Button>
            </Link>
            {!loading && (
              isLoggedIn ? (
                <>
                  {isShopOwner && (
                    <Link href="/shop/dashboard">
                      <Button variant="ghost" size="sm">Dashboard</Button>
                    </Link>
                  )}
                  <Link href="/user/diagnosis-history">
                    <Button variant="ghost" size="sm">History</Button>
                  </Link>
                  <LogoutButton />
                </>
              ) : (
                <>
                  <Link href="/auth/login">
                    <Button variant="ghost" size="sm">Login</Button>
                  </Link>
                  <Link href="/auth/sign-up">
                    <Button variant="ghost" size="sm">Sign Up</Button>
                  </Link>
                </>
              )
            )}
          </nav>
        </div>
      </header>
    )
  }

  return (
    <header className="border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container mx-auto flex h-16 items-center justify-between px-4">
        <div className="flex items-center gap-4">
          {showBackButton && onBack && (
            <Button 
              variant="ghost" 
              size="sm"
              onClick={onBack}
              className="gap-2"
            >
              <ArrowLeft className="h-4 w-4" />
              Back to Diagnosis
            </Button>
          )}
          <Link href="/" className="flex items-center gap-2">
            <Image 
              src="/logo.png" 
              alt="FixWise Logo" 
              width={180} 
              height={60}
              className="h-12 w-auto"
            />
          </Link>
        </div>
        <nav className="flex items-center gap-4">
          <Link href="/">
            <Button variant="ghost">Home</Button>
          </Link>
          <Link href="/shops">
            <Button variant="ghost">Find Shops</Button>
          </Link>
          {!loading && (
            isLoggedIn ? (
              <>
                {isShopOwner && (
                  <Link href="/shop/dashboard">
                    <Button variant="ghost">Dashboard</Button>
                  </Link>
                )}
                <Link href="/user/diagnosis-history">
                  <Button variant="ghost">History</Button>
                </Link>
                <LogoutButton />
              </>
            ) : (
              <>
                <Link href="/auth/login">
                  <Button variant="ghost">Login</Button>
                </Link>
                <Link href="/auth/sign-up">
                  <Button variant="outline">Sign Up</Button>
                </Link>
              </>
            )
          )}
        </nav>
      </div>
    </header>
  )
}

